.mealsWindow {

    display: flex;
    flex-direction: column;
    border-radius: 30px;
    align-items: center;
    margin-top: 2rem auto;
    background-color: yellowgreen;
    min-height: 20rem;
    max-height: fit-content;
    width: 55rem;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    padding-top: 0rem;
    padding-bottom: 1.3rem;
    margin-top: -8rem;
}

.mealError {
   text-align: center;
    font-size: medium;
    color:crimson;
    font-weight: bold;
    font-family:'Courier New', Courier, monospace;
}

.mealSection {
    display: block;
    border-bottom: 2px solid gray;

}

@media (min-width: 768px) {
    .modal {
      width: 40rem;
      left: calc(50% - 20rem);
    }
  }

  @keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }